import React from 'react';
import {Form,Button,Nav} from 'react-bootstrap';
import { FacebookLoginButton } from 'react-social-login-buttons';


const Form1 = () => {
     return(
<div className="division" style={{width:"100%",backgroundColor:"red" }}>
 
<Form style={{width:"80%", paddingRight:"10%" ,paddingLeft:"10%",backgroundColor:"red" }}>
  <Form.Group className="mb-3" >
    <Form.Label>Last name</Form.Label>
    <Form.Control type="text" placeholder="Enter your last name" required/>
  </Form.Group>
  <Form.Group className="mb-3">
    <Form.Label>First name</Form.Label>
    <Form.Control type="text" placeholder="Enter your first name" required/>
  </Form.Group>
  <Form.Group className="mb-3" >
    <Form.Label>Email address</Form.Label>
    <Form.Control type="email" placeholder="Enter email" required/>
    <Form.Text className="text-muted">
      We'll never share your email with anyone else.
    </Form.Text>
  </Form.Group>
  <Form.Group className="mb-3">
    <Form.Label>Birth date</Form.Label>
    <Form.Control type="date" required/>
  </Form.Group>
  <Form.Group className="mb-3">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" placeholder="Enter password here"  required/>
    <Form.Text className="text-muted">
      It's mandatory to choose a Password.
    </Form.Text>
  </Form.Group>
  <Form.Group className="mb-3">Gender:</Form.Group>
  <Form.Group className="mb-3">
    <Form.Label for="male">Male</Form.Label>
    <Form.Check  type="radio" name="gender" id="male" value="male" required/>
    <Form.Label for="female">Female</Form.Label>
    <Form.Check  type="radio" name="gender" id="female" value="female" required/>
  </Form.Group>
  <Form.Group className="mb-3">
    <Form.Check type="checkbox" label="Check me out" />
  </Form.Group>
  <div className="division" style={{paddingBottom:"10%"  }}> 
  <Form.Group className="division">
  <Button className="mt-3 mb-3" variant="dark" type="submit" style={{height:"70%" }}>
    Sign in
  </Button>
  <Nav.Link  href="https://fr-fr.facebook.com/" className="mt-3 mb-3">
  <FacebookLoginButton style={{width:"100%"}} />
  </Nav.Link> 
  </Form.Group>
  </div>
</Form>
</div>
     )
}
export default Form1;
